<template>
    <div>
        <div class="w-full sm:w-2/3 md:w-1/3  sm:m-12 mt-5 sm:mb-20">
          <form class="bg-white shadow-2xl rounded px-8 pt-6 pb-10 mb-6">

            <div class="mb-4">
              <label class="block text-gray-700 text-sm font-bold mb-2" for="email">
                Date in the future
              </label>
              <input v-model="dfuture" class=" shadow appearance-none border border-stone-400 rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="dfuture" type="text" placeholder="">
            </div>

            <div class="mb-4">
              <label class="block text-gray-700 text-sm font-bold mb-2" for="email">
                Title
              </label>
              <input v-model="title" class="shadow appearance-none border border-stone-400 rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="title" type="text" placeholder="">
            </div>

            <div class="mb-10">
                
                <label for="details" class="block text-gray-700 text-sm font-bold mb-2 dark:text-white">
                    Details
                </label>
                <textarea v-model="details" id="details" rows="4" class="block p-2.5 w-full text-sm text-gray-900 bg-gray-50 rounded-lg border border-stone-400 h-72 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" ></textarea>
            </div>
            <div class="flex justify-center">
              <button @click="onSave" class="bg-blue-999  sm:w-1/3 w-3/5 hover:bg-blue-700 text-white py-2 px-4 rounded-xl focus:outline-none focus:shadow-outline" type="button">
                Save
              </button>
            </div>
          </form>
        </div>
    </div>
</template>

<script>
import uuid from 'uuid';
    export default {
        name: 'AddItem',
        data(){
          return {
            dfuture: '',
            title: '',
            details: ''
          }
        },
        methods:{
          onSave(e){
            e.preventDefault();
            if (!this.title) {
              alert('Please add title');
              return
            }
            const newItem = {
              id: uuid(),
              dfuture: this.dfuture,
              title: this.title,
              details: this.details
            }

            axios.post('https://eventful-moments.onrender.com/api/v1/moment', newItem)
            .then((res)=>{console.log(res)});

            this.dfuture = ''
            this.title= ''
            this.details = ''

          },

        }
        
    }
</script>