<template>
    <div class="w-full sm:w-2/3 md:w-1/3 m-auto md:mt-20">
  <form method="post" class="bg-white shadow-2xl rounded px-8 pt-6 pb-8 " action="https://eventful-moments.onrender.com/api/v1/users/login">
    <h1 class="text-3xl font-bold">Welcome Back,</h1>
    <p class="mb-6">Hi, my name is Eventful Moments, I am a bucket… no, not the bucket of water but I store awesome moments you will like to have in coming years.</p>
    <div class="mb-4">
      <label class="block text-gray-700 text-sm font-bold mb-2" for="email">
        Email
      </label>
      <input v-model="email" name="email" class="shadow appearance-none border border-black rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="email" type="email" placeholder="">
    </div>
    <div class="mb-6">
      <label class="block text-gray-700 text-sm font-bold mb-2" for="password">
        Password
      </label>
      <input v-model="password" name="password" class="shadow appearance-none border border-black rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" id="password" type="password">
      
    </div>
    <div class="flex items-center justify-between">
      <button @click="logIn" class="bg-blue-999 m-auto w-3/5 hover:bg-blue-700 text-white py-2 px-4 rounded-xl focus:outline-none focus:shadow-outline" type="button">
        Login
      </button>
    </div>
  </form>
</div>

</template>

<script>

    import router from "@/router";
import axios from "axios";

    export default {
        name: 'Form',
        data(){
          data: []
        },
        methods:{
          async logIn(){
            const res = await axios.post('https://eventful-moments.onrender.com/api/v1/users/login',{ email: this.email, password: this.password});
            this.data = res.data;
            console.log(this.data);
            this.persist()
            router.replace('/my-bucket');
            
          },
          persist(){
            localStorage.token = this.data.token;
          }
        }
    }
</script>